.order {
  .offset(30px 0; 0);
  background-color: @white4;

  @media (min-width: @tablet-width) {
    padding: 100px 0 127px 0;
  }
}

.order__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(188px, 1fr));
  column-gap: 15px;
  row-gap: 49px;
  align-items: stretch;
  .offset(0; 0 0 57px 0);
  list-style: none;
}

.order__item {
  display: flex;
  position: relative;
  max-width: 188px;
  min-height: 170px;
  .offset(50px 30px 0 29px; 0 auto);
  border: 2px solid @blue;
  border-radius: 20px;
  z-index: 10;

  &::before {
    content: "";
    position: absolute;
    top: -32px;
    left: 62px;
    width: 64px;
    height: 64px;
    background-repeat: no-repeat;
    background-size: 64px 64px;
    z-index: -1;
  }
}

.order__item:nth-child(n+3) {
  padding-right: 23px;
  padding-left: 28px;
}

.order__item-generate(@n) when (@n > 0) {
  .order__item-@{n} {
    &::before {
      background-image: url("../img/icon-order-item-@{n}.svg");
    }
  }
  .order__item-generate(@n - 1);
}

.order__item-generate(4);

.order__item-inner {
  .offset(0; 0 0 auto 0);
}

.order__description {
  .offset(0; 0);
  .typography(14px; 20px);
}

.order__phone-link {
  display: block;
  .offset(0; 7px 0 0 0);
  font-weight: 700;
  .typography(16px; 24px);
  color: @grey4;
  text-align: left;
  transition: 0.3s ease;

  &:hover,
  &:focus {
    color: @blue2;
    transition: 0.3s ease;
  }

  &:active {
    opacity: 0.7;
  }
}
